* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.card {
  background: #bdbaba;
  width: 20rem;
  padding: 3rem 1rem;
  margin: 2rem auto;
  transition: all 0.3s linear;
  border-radius: 0.3rem;
  text-align: center;
}
.card .img {
  border-radius: 50%;
  border: 0.1rem solid teal;
}

.card .name {
  text-transform: capitalize;
  margin: 0.5rem 0;
  letter-spacing: 0.15rem;
  color: teal;
}
.card:hover {
  box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.3);
}
.des{
  color: darkgreen;
  padding: 10px;
  font-size: 14px;
}